.ExperienceCard {
  display: grid;
  grid-template-rows: 9% 8% 5% 10% 1fr;
  margin-top: 3vh;
  margin-left: auto;
  margin-right: auto;
  padding-top: 1vh;
  padding-bottom: 1vh;
  height: 65vh;
  min-height: 1300px;
  width: 26vw;
  border: 2px solid rgba(0,0,0,0.7);
  border-radius: 3%;
  box-shadow: 0 5px 10px -2px gray;
}

.ExperienceCard h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: calc(2px + 1.5vh);
  margin-bottom: 0;
  font-family: var(--default-text-font);
  font-size: var(--h1-content-font-size-small);
  color: var(--default-title-color);
}

.ExperienceCard h2 {
  display: block;
  margin-top: calc(2px + 1vh);
  margin-bottom: 0;
  font-family: var(--default-text-font);
  font-size: var(--h2-content-font-size-small);
  color: var(--default-title-color);
}

.ExperienceCard p {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 95%;
  margin-top: calc(2px + 2vh);
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  font-family: var(--default-text-font);
  font-size: var(--text-content-font-size-small);
  color: rgba(0,0,0,0.7);
}

.ExperienceCard ul {
  width: 80%;
  margin: auto;
  padding-left: 2vw;
  padding-bottom: 1vh;
  text-align: left;
  font-family: var(--default-text-font);
  font-size: var(--text-content-font-size-small);
  color: rgba(0,0,0,0.7);
}

.ExperienceCard li {
  margin-top: 1vh;
  list-style-type: disc !important;
}

.nonDate {
  color: rgba(0,0,0,0.7);
}

.titleSeparator {
  margin-top: calc(2px + 1.5vh);
  color: rgba(0,0,0,0.7);
  border-top: 2px solid rgba(0,0,0,0.7);
}

@media only screen and (max-width: 3000px)  {
  .ExperienceCard {
    height: 65vh;
    min-height: 820px;
  }
}

@media only screen and (max-width: 2000px)  {
  .ExperienceCard {
    height: 65vh;
    min-height: 700px;
  }
}

@media only screen and (max-width: 1500px)  {
  .ExperienceCard {
    height: auto;
    min-height: 385px;
  }
}


@media only screen and (max-width: 1000px)  {
  .ExperienceCard {
    height: auto;
    min-height: 300px;
  }
}

@media only screen and (max-width: 600px)  {
  .ExperienceCard {
    margin-top: 3vh;
    width: 80%;
    min-height: 250px;
  }

  .ExperienceCard h1 {
    margin-top: 0.2vh;
    font-size: var(--h1-content-font-size-small);
  }

  .ExperienceCard h2 {
    margin-top: calc(2px + 0.3vh);
    font-size: var(--h2-content-font-size-small);
  }

  .ExperienceCard p {
    font-size: var(--text-content-font-size-small);
  }

  .ExperienceCard ul {
    font-size: var(--text-content-font-size-small);
  }

  .titleSeparator {
    margin-top: calc(2px + 1vh);
  }
}