@import url(//fonts.googleapis.com/css?family=Playfair+Display);
@import url(//fonts.googleapis.com/css?family=Merriweather);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(241, 238, 241);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* vw will be used for scalable text depending on window size, rem is faulty when a user's font
in browser is different (chrome://settings/fonts). I can use px but decided to use vw for scaling on 
large and small devices. Readability, I think, is not an issue as the font size is big enough. */

:root{
  --nav-bar-height: 7vh;
  --default-header-font: 'Playfair Display';
  --default-text-font: 'Merriweather';
  --default-title-font: 'Merriweather';
  --default-highlight-color: rgba(29, 233, 206, 0.966);
  --default-header-color: rgba(22, 182, 161, 0.966);
  --default-title-color: rgba(11, 155, 136, 0.966);
  --default-margin: 14px;
  --default-margin-top: 1.9vh;
  --default-margin-left: 2vw;
  --navigation-percent: 70%;
  --fontsize-nav: calc(5px + 1.5vw);
  --h1-content-font-size: 3.5vw;
  --h1-content-font-size-small: calc(2px + 2vw);
  --h2-content-font-size-small: calc(2px + 1.3vw);
  --text-content-font-size: 1.8vw;
  --text-content-font-size-small: calc(2px + 1.35vw);
}

.App {
  display: grid;
  grid-template-rows: 0;
}

@media only screen and (max-width: 3000px) {
  :root{
    --text-content-font-size-small: calc(2px + 1.5vw);
  }
}

@media only screen and (max-width: 1500px) {
  :root{
    --fontsize-nav: calc(2px + 1.7vw);
    --default-margin: 8px;
    --navigation-percent: 68%;
    --text-content-font-size-small: calc(2px + 1.2vw);
  }
}

@media only screen and (max-width: 1000px) {
  :root{
    --fontsize-nav: calc(2px + 2vw);
    --default-margin: 8px;
    --default-margin-top: 2.5vh;
    --navigation-percent: 55%;
    --h1-content-font-size: 5vw;
    --text-content-font-size: 1.9vw;
  }
}

@media only screen and (max-width: 600px) {
  :root{
    --nav-bar-height: 5vh;
    --fontsize-nav: 3.5vw;
    --default-margin: 2.2vh;
    --default-margin-top: 1.7vh;
    --default-margin-left: 4vw;
    --navigation-percent: 34%;
    --h1-content-font-size: 6vw;
    --h1-content-font-size-small: calc(2px + 4vw);
    --h2-content-font-size-small: calc(2px + 2.5vw);
    --text-content-font-size: 4vw;
    --text-content-font-size-small: calc(2px + 2.8vw);
  }
}
.NavigationBar {
    background-color: rgba(0,0,0,0.6);
    height: var(--nav-bar-height);
    min-height: 90px;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center; 
}

.navigationItems {
  width: 95%;
  display: grid;
  grid-template-columns: var(--navigation-percent) 1.1fr 0.9fr 0.9fr;
}

.active {
    font-weight: bolder;
}

@media only screen and (max-width: 3000px) {
  .NavigationBar {
    min-height: 60px;
  }
}

@media only screen and (max-width: 1500px) {
  .NavigationBar {
    min-height: 50px;
  }
}

@media only screen and (max-width: 600px) {
    .NavigationBar {
        background-color: rgba(0,0,0,0.7);
        grid-template-columns: var(--navigation-percent) 0.9fr 0.75fr 1fr;
        min-height: 40px;
    }
}
.NavigationItem {
  margin-left: 1vw;
  font-family: var(--default-header-font);
  font-style: normal; 
  font-feature-settings: normal; 
  font-variant: normal; 
  font-size: var(--fontsize-nav);
  text-decoration: none;
  color: white;
}

.NavigationActive {
  font-weight: bolder;
  color: var(--default-highlight-color);
}

li {
  list-style-type: none !important;
}

@media only screen and (max-width: 600px) {
  .NavigationItem {
    margin-left: 1.5vw;
  }
}


.Header {
  background-image: url(/portfolio-project/static/media/header-desktop-2.d52e4f64.jpg);
  background-size: cover;
  background-position: center;
  z-index: 0;
  width: 100%;
  height: 60vh;
  min-height: 700px;
  font-size: calc(10px + 1vw);
  color: white;
  box-shadow: 0 5px 10px -2px gray;
}

.HeaderName{
  font-family: var(--default-header-font);
  position: relative;
  width: 50%;
  top: 40%;
  left: 30vw;
  color: white;
}

.HeaderRole{
  font-family: var(--default-header-font);
  position: relative;
  width: 50%;
  top: 40%;
  left: 30vw;
  color: var(--default-highlight-color);
}

.HeaderButton{
  background-color: var(--default-highlight-color);
  width: 11% !important;
  height: 70px !important;
  border-width: 1px;
  border-radius: 4vh;
  border-color: var(--default-highlight-color);
  cursor: pointer;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 45%;
  left: 30vw;
  font-family: var(--default-text-font);
  font-size: calc(7px + 1vw);
}

.HeaderButton:hover{
  background-color: var(--default-header-color);
}

.HeaderButton:focus{
  outline: none;
}

